#app-store {
  height: 127px;
  background: #F5F3F8;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#app-store p {
  align-self: center;
  margin: 0;
  font-family: "NicoMoji";
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 24px;
  /* or 86% */

  letter-spacing: -0.015em;

  color: #4349CD;
}

#logo-text {
  display: flex;
  justify-content: center;
  padding-top: 24px;
}

#app-store img {
  width: 276px;
  height: 42px;
}