ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#navbar {
  font-family: "NicoMoji";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 11px;

  text-align: center;
  letter-spacing: -0.04em;
  color: #4349CD;
  padding: 9px 16px;

  background-color: rgba(245, 243, 248, 1);
}

#navbar ul {
  display: flex;
  justify-content: space-between;
}